body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("https://fonts.gstatic.com/s/roboto/v18/KFOlCnqEu92Fr1MmWUlfBBc4AMP6lQ.woff2") format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("https://fonts.gstatic.com/s/roboto/v18/KFOmCnqEu92Fr1Mu4mxKKTU1Kg.woff2") format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; }

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("https://fonts.gstatic.com/s/opensans/v15/mem5YaGs126MiZpBA-UN7rgOUuhpKKSTjw.woff2") format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; }

.App {
  text-align: center;
  background-color: #282c34;
  display: block; }
  .App h1 {
    font-family: 'Open Sans', sans-serif;
    font-weight: 700; }
  .App button {
    font-family: 'Roboto', sans-serif;
    font-weight: 700; }
  .App span {
    font-family: 'Roboto', sans-serif;
    font-weight: 400; }

.charts {
  background-color: #282c34;
  font-size: 15px;
  color: white; }

.label {
  text-anchor: middle;
  fill: white;
  font-size: 12px; }

#lineGraphs,
#pieCharts {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }

.tooltip {
  visibility: hidden; }

.arc:hover .tooltip {
  visibility: visible; }

.floating-label {
  font-size: 12px; }

.line-path {
  stroke-width: 1.5px;
  stroke: #d6d6d8;
  fill: none; }

.axis {
  stroke: #d6d6d8; }

.axis-labels text {
  fill: #a8a8ff;
  font-size: 9px;
  text-anchor: middle; }

.axis-labels line {
  stroke: #d6d6d8; }

.graph-lines {
  opacity: .2; }

.legend {
  height: 29px; }
  .legend .close,
  .legend .high,
  .legend .low,
  .legend .open {
    height: 10px;
    width: 10px;
    margin: 0 auto;
    display: inline-block;
    border-radius: 50%;
    vertical-align: middle; }
  .legend span {
    font-size: 13px;
    vertical-align: middle; }

.open {
  background-color: #95eaf1; }

.high {
  background-color: #ef6e8d; }

.close {
  background-color: #ff3c3c; }

.low {
  background-color: #a8a8ff; }

.high + span,
.low + span,
.open + span {
  padding-right: 13px; }

.hover-holder {
  position: absolute;
  padding: 13px;
  background-color: #ef6e8d;
  border-radius: 8px;
  border: 1px solid white;
  z-index: 999; }
  .hover-holder span {
    font-size: 13px;
    display: block;
    text-align: left;
    color: black; }
    .hover-holder span:first-of-type {
      padding-bottom: 4px; }

.select-input__single-value {
  color: #ef6e8d !important;
  background-color: #282c34;
  font-family: 'Roboto', sans-serif;
  font-weight: 700; }

.select-input__control .select-input__dropdown-indicator {
  color: white; }

.select-input__indicator-separator {
  display: none; }

.select-input__option {
  font-family: 'Roboto', sans-serif;
  font-weight: 700; }

.select-input__control--is-focused .select-input__dropdown-indicator {
  color: #ef6e8d; }

.select-input__control--menu-is-open .select-input__dropdown-indicator {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }

.select-input__menu-list::-webkit-scrollbar {
  width: 8px;
  height: 0; }

.select-input__menu-list::-webkit-scrollbar-track {
  background: #282c34;
  box-shadow: inset 0 0 1px #95eaf1; }

.select-input__menu-list::-webkit-scrollbar-thumb {
  background: #95eaf1;
  border-radius: 3px; }

.select-input__menu-list::-webkit-scrollbar-thumb:hover {
  background: #ef6e8d; }

